// Basic setup. Most of the really basic stuff is included in "critical.scss".
@import "base/variables";

// Mixins, helpers and similar. "Helpers" are included in the critical file.
@import "utilities/functions";
@import "utilities/mixins";
@import "utilities/placeholders";
@import "utilities/animations";

// Layout is included in the critical file.
@import "vendor/swiper";

// All NON-CRITICAL components, since the critical ones should've been loaded using the inline styling already (see "critical.scss").
@import "components/noncritical/some-noncritical-component.scss";

body {
    background: #499dff;
    background: -moz-linear-gradient(-45deg, #499dff 0%, #8349fb 90%);
    background: -webkit-linear-gradient(-45deg, #499dff 0%, #8349fb 90%);
    background: linear-gradient(135deg, #499dff 0%, #8349fb 90%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#499dff', endColorstr='#8349fb', GradientType=1);
}

$color--white: #e2f4fe;
$color--black: #434346;

@keyframes shake {

    10%,
    90% {
        transform: translate3d(-1px, 0, 0);
    }

    20%,
    80% {
        transform: translate3d(2px, 0, 0);
    }

    30%,
    50%,
    70% {
        transform: translate3d(-4px, 0, 0);
    }

    40%,
    60% {
        transform: translate3d(4px, 0, 0);
    }
}

.stop-container {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;

    h2 {
        @include position(absolute, 50px 0 null 0);
        color: $color--white;
        text-align: center;
        transition: opacity 250ms ease-in;

        .swiper-ready & {
            opacity: 1;
        }
    }
}

.swiper-container {
    opacity: 0;
    height: calc(50vh + 20px);

    &.swiper-container-initialized {
        opacity: 1;
    }


    .swiper-slide {
        display: flex;
        flex-direction: row;
        justify-content: center;
        width: calc(100vw - 60px);
        height: 50vh;

        &.swiper-slide-active {
            .swiper-content {
                transform: translateY(0);
            }
        }

        .swiper-content {
            background-color: $color--white;
            transform: translateY(20px);
            transition: transform 350ms ease-in-out;
            border-radius: 7px;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            position: relative;
            width: 100%;
            padding: 20px 20px;
        }

        .question {
            p {
                margin: 0 0 10px 0;
                font-size: 22px;
                color: $color--black;
            }
        }

        form {
            .input-answer {
                display: flex;
                flex-direction: row;
            }

            input {
                border: 0;
                background-color: white;
                color: $color--black;
                @include placeholder($color--black);
                outline: none;
                font-size: 22px;
                padding-left: 10px;
                border-radius: 5px;
                height: 50px;
                width: 100%;
                font-family: Helvetica, sans-serif;
                letter-spacing: 0.5px;

                &.shake {
                    animation-name: shake;
                    animation-duration: 500ms;
                    animation-fill-mode: both;
                }
            }

            button {
                outline: none;
                border: 0;
            }
        }
    }
}

.swiper-pagination-progressbar {
    height: 4px;
    width: 100%;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.1);

    .swiper-pagination-progressbar-fill {
        background: $color--white;
    }
}

.nextQuestion {
    @include clean-button;
    outline: none;
    @include position(absolute, null 0 0 0);
    text-align: center;
    width: 100%;
    color: $color--white;
    text-transform: uppercase;
    font-weight: bold;
    height: 65px;
}


.ratio-16-9 {
    @include aspect-ratio(16, 9);
    text-align: center;
    margin: 20px 0;

    img {
        height: 100%;
        width: auto;
        margin: auto;
    }
}

.modal__close {
    height: 40px;
    width: 40px;

    &:before,
    &:after {
        content: '';
        @include position(absolute, 10px null null null);
        height: 50%;
        width: 1px;
        background-color: $color--black;
    }

    &:before {
        transform: rotate(45deg);
    }

    &:after {
        transform: rotate(-45deg);
    }
}

.modal__wrapper.modal__wrapper--img-modal {
    transform: none;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .modal__header {
        position: absolute;
        top: 0;
        right: 0;
    }

    img {
        display: block;
    }
}

.modal__wrapper--tip-modal,
.modal__wrapper--end-modal {
    border-radius: 7px;

    .modal__title-wrapper,
    .modal__content--tip-modal {
        text-align: center;
    }

    .modal__close {
        position: absolute;
        right: 0;
        top: 7px;
    }

    .modal__content--end-modal {
        text-align: center;
    }

    .nextPosition {
        display: inline-flex;
        margin-bottom: 20px;
        color: $color--white;
        text-decoration: none;
        background: #499dff;
        background: -moz-linear-gradient(-45deg, #499dff 0%, #8349fb 90%);
        background: -webkit-linear-gradient(-45deg, #499dff 0%, #8349fb 90%);
        background: linear-gradient(135deg, #499dff 0%, #8349fb 90%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#499dff', endColorstr='#8349fb', GradientType=1);
        padding: 10px 15px;
        border-radius: 7px;
    }
}
