body {
  background: #499dff;
  background: -moz-linear-gradient(-45deg, #499dff 0%, #8349fb 90%);
  background: -webkit-linear-gradient(-45deg, #499dff 0%, #8349fb 90%);
  background: linear-gradient(135deg, #499dff 0%, #8349fb 90%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#499dff', endColorstr='#8349fb', GradientType=1);
}

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}

.stop-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.stop-container h2 {
  position: absolute;
  top: 50px;
  right: 0;
  left: 0;
  color: #e2f4fe;
  text-align: center;
  transition: opacity 250ms ease-in;
}

.swiper-ready .stop-container h2 {
  opacity: 1;
}

.swiper-container {
  opacity: 0;
  height: calc(50vh + 20px);
}

.swiper-container.swiper-container-initialized {
  opacity: 1;
}

.swiper-container .swiper-slide {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: calc(100vw - 60px);
  height: 50vh;
}

.swiper-container .swiper-slide.swiper-slide-active .swiper-content {
  transform: translateY(0);
}

.swiper-container .swiper-slide .swiper-content {
  background-color: #e2f4fe;
  transform: translateY(20px);
  transition: transform 350ms ease-in-out;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: relative;
  width: 100%;
  padding: 20px 20px;
}

.swiper-container .swiper-slide .question p {
  margin: 0 0 10px 0;
  font-size: 22px;
  color: #434346;
}

.swiper-container .swiper-slide form .input-answer {
  display: flex;
  flex-direction: row;
}

.swiper-container .swiper-slide form input {
  border: 0;
  background-color: white;
  color: #434346;
  outline: none;
  font-size: 22px;
  padding-left: 10px;
  border-radius: 5px;
  height: 50px;
  width: 100%;
  font-family: Helvetica, sans-serif;
  letter-spacing: 0.5px;
}

.swiper-container .swiper-slide form input::-moz-placeholder {
  color: #434346;
}

.swiper-container .swiper-slide form input::-webkit-input-placeholder {
  color: #434346;
}

.swiper-container .swiper-slide form input:-ms-input-placeholder {
  color: #434346;
}

.swiper-container .swiper-slide form input.shake {
  animation-name: shake;
  animation-duration: 500ms;
  animation-fill-mode: both;
}

.swiper-container .swiper-slide form button {
  outline: none;
  border: 0;
}

.swiper-pagination-progressbar {
  height: 4px;
  width: 100%;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.1);
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background: #e2f4fe;
}

.nextQuestion {
  appearance: none;
  background: transparent;
  border: 0;
  border-radius: 0;
  cursor: pointer;
  outline: none;
  padding: 0;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
  vertical-align: bottom;
  outline: none;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  text-align: center;
  width: 100%;
  color: #e2f4fe;
  text-transform: uppercase;
  font-weight: bold;
  height: 65px;
}

.ratio-16-9 {
  position: relative;
  overflow: hidden;
  text-align: center;
  margin: 20px 0;
}

.ratio-16-9:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 56.25%;
}

.ratio-16-9 > .content,
.ratio-16-9 > img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.ratio-16-9 > img {
  width: 100%;
  height: 100%;
}

.ratio-16-9 img {
  height: 100%;
  width: auto;
  margin: auto;
}

.modal__close {
  height: 40px;
  width: 40px;
}

.modal__close:before,
.modal__close:after {
  content: '';
  position: absolute;
  top: 10px;
  height: 50%;
  width: 1px;
  background-color: #434346;
}

.modal__close:before {
  transform: rotate(45deg);
}

.modal__close:after {
  transform: rotate(-45deg);
}

.modal__wrapper.modal__wrapper--img-modal {
